export default {
  layout: 'login',
  data() {
    return {
      userForm: {
        name: 'root',
        pass: '123456'
      },
      rules: {
        name: [{ required: true, message: '请输入用户名/邮箱/手机', trigger: 'blur' }],
        pass: [{ required: true, message: '请输入密码', trigger: 'blur' }],
      },
    }
  },
  fetch() {
    this.$store.commit("setUserMenu", [])
  },
  mounted(){
    this.$localForage.removeItem('user')
    window.addEventListener('keydown',this.keyDown)
  },
  destroyed(){
    window.removeEventListener('keydown',this.keyDown,false);
  },
  methods: {
    async submitForm(formName) {
      const valid = await this.$refs[formName].validate()
      if (valid) {
        const login = await this.$axios.$post('/api/admin/user/login', {
          name: this.userForm.name,
          password: this.userForm.pass,
        })
        if (login.success) {
          // 用户登录 缓存
          console.log("login", login.data)
          await this.$localForage.setItem('user', login.data)
          this.$store.commit('setUser', login.data)
          this.$notify({
            title: `欢迎您，${login.data.name}`,
            // message: h('i', { style: 'color: teal'}, '这是提示文案这是提示文案这是提示文案这是提示文案这是提示文案这是提示文案这是提示文案这是提示文案'),
            duration: 1500,
            offset: 70
          })
          this.$router.push(this.localePath('/'))
        } else {
          this.$message({
            showClose: true,
            message: `${login.message}`,
            type: 'error',
          })
        }
      } else {
        console.log('error submit!!')
        return false
      }
    },
    keyDown(e){
        //如果是回车则执行登录方法
      if(e.keyCode == 13){
        this.submitForm('userForm');
      }
    }
  },
}
